body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(./images/bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100vw;
  margin: 0;
  font-family: "Be Vietnam Pro", sans-serif;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
  height: auto;
}

.container-app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 750px;
  height: 450px;
  background-color: #343964;
  border-radius: 10px;
}

.container-numbersQuiz {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 600px;
  height: 100px;
  margin-bottom: 0;
}

.title-country {
  font-size: 15px;
  color: #8b8eab;
}

.options-quiz {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 15px;
  color: #e2e4f3;
}

.quiz-number {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #e2e4f3;
  font-weight: 700;
  font-size: 1rem;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 25px;
  background-color: #393f6e;
}

.quiz-number:hover {
  opacity: 0.6;
}

.container-question {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #e2e4f3;
  font-size: 1.3rem;
  width: 400px;
  height: 100px;
  margin-bottom: 10px;
}

.container-options {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 550px;
  height: 170px;
  gap: 30px;
  margin-bottom: 30px;
}

.options {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 240px;
  height: 65px;
  color: #e2e4f3;
  font-weight: 400;
  background-color: #393f6e;
  border-radius: 15px;
}

.options:hover {
  opacity: 0.7;
}

.hidden {
  display: none;
}

.background-color-none {
  background-color: transparent;
}

.container-completed {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #343964;
  width: 400px;
  height: 420px;
  border-radius: 15px;
}

.container-img-completed {
  display: flex;
  width: 349px;
  height: 107px;
  background-image: url("/src/images/congrats.svg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin-bottom: 15px;
}

.container-congrats {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #e2e4f3;
  font-size: 1.5rem;
  width: 330px;
  height: 80px;
}

.container-puntuacion {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #e2e4f3;
  font-size: 1rem;
  width: 330px;
  height: 50px;
}

.container-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 350px;
  height: 120px;
}

.button-again {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #e2e4f3;
  font-size: 1.1rem;
  font-weight: 500;
  background: linear-gradient(#e65895, #bc6be8);
  border: none;
  cursor: pointer;
  width: 230px;
  height: 60px;
  border-radius: 15px;
}

.button-again:hover {
  opacity: 0.6;
}

.quiz-selected {
  background: linear-gradient(#e65895, #bc6be8);
}

.img-flag {
  width: 30px;
  height: 25px;
}

.img-coaOfArmas {
  width: 75px;
  height: 55px;
}

.option-selected {
  background-color: #e65895;
}

.svg-target {
  margin-left: 10px;
}

@media (max-width: 800px) {
  .container-app {
    width: 600px;
    height: auto;
  }

  .options-quiz {
    width: 420px;
    height: auto;
    flex-wrap: wrap;
  }

  .container-numbersQuiz {
    height: auto;
    margin-top: 20px;
  }

  .container-options {
    margin-bottom: 60px;
  }
}
